.header {
  display: flex;
  justify-content: space-between;
  padding: 0 50px;
  align-items: center;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.05);
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0;
  text-align: left;

  &__logo {
    width: 40px;
    height: 40px;
  }
}
